* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
.backsidet1{
  width: 1000px;
}
.backsidet1 table {
  
  border: 2px solid rgba(0, 0, 0, 0.3);
  color: #234783;
}
table th,
td {
  text-align: center;
  border: 2px solid #234783;
}
.table_input {
  background: transparent;
  border: none;
  outline: none;
  padding-left: 10px;
  font-weight: 500;
  letter-spacing: 1px;
  font-size: 16px;
  color: #234783;
}

.table1 {
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 1px;
  text-align: center;
  font-size: 16px;
}
